<template>
    <div>
        <Header title="管理员管理"></Header>
        <el-button type="primary" @click="addAdmin">添加管理员</el-button>
        <el-table
                :data="admins"
                border
                stripe
                v-loading="dataLoading"
                style="width: 100%;margin-top: 20px">
            <template slot="empty">
                暂无数据，<el-link @click="getData" type="primary">重新加载</el-link>
            </template>
            <el-table-column
                    prop="id"
                    label="ID"
                    align="center"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    prop="username"
                    align="center"
                    label="用户名"
                    min-width="180">
            </el-table-column>
            <el-table-column
                    min-width="240"
                    align="center"
                    label="操作">
                <template slot-scope="scope">
                    <el-button type="warning" @click="editPassword(scope.row)">修改密码</el-button>
                    &nbsp;
                    <el-popconfirm
                        :title="`确认删除管理员${scope.row.username}么？此操作不可恢复！`"
                        @confirm="remove(scope.row)"
                    >
                        <el-button
                            :disabled="scope.row.id === currentAdmin.id"
                            :title="scope.row.id === currentAdmin.id ? '无法删除当前登录用户' : ''"
                            :loading="scope.row.removeLoading"
                            type="danger"
                            slot="reference">
                            删除
                        </el-button>
                    </el-popconfirm>
                </template>
            </el-table-column>
        </el-table>
        <div class="text-center" style="padding-top: 10px;">
            <el-pagination
                    hide-on-single-page
                    background
                    :page-size.sync="size"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="page"
                    @current-change="getData"
                    :total="total">
            </el-pagination>
        </div>
        <el-dialog
            title="添加管理员"
            width="360px"
            center
            :visible.sync="dialogAddFormVisible"
            :close-on-press-escape="false"
            :close-on-click-modal="false"
        >
            <el-form ref="addForm" :model="addFormData" label-width="100px" label-position="left" :rules="addFormRules" @keydown.native.enter="doAddAdmin">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model.trim="addFormData.username" placeholder="请输入用户名" ref="focus1"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="addFormData.password" show-password placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item label="重复密码" prop="password2">
                    <el-input v-model="addFormData.password2" show-password placeholder="请再次输入密码"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button :loading="addLoading" type="primary" @click="doAddAdmin">添 加</el-button>
            </div>
        </el-dialog>
        <el-dialog
                title="修改密码"
                width="420px"
                center
                :visible.sync="dialogEditPasswordFormVisible"
                :close-on-press-escape="false"
                :close-on-click-modal="false"
        >
            <el-form ref="editPasswordForm" :model="editPasswordFormData" label-width="100px" label-position="left" :rules="editPasswordFormRules"  @keydown.native.enter="doEditPassword">
                <el-form-item label="ID">
                    <el-input :value="editPasswordFormData.id" readonly></el-input>
                </el-form-item>
                <el-form-item label="用户名">
                    <el-input :value="editPasswordFormData.username" readonly></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="password">
                    <el-input v-model="editPasswordFormData.password" show-password placeholder="请输入新密码" ref="focus2"></el-input>
                </el-form-item>
                <el-form-item label="重复新密码" prop="password2">
                    <el-input v-model="editPasswordFormData.password2" show-password placeholder="请再次输入新密码"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button :loading="editPasswordLoading" type="primary" @click="doEditPassword">修 改</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {queryByPage, deleteAdmin, addAdmin, changePassword} from "../api/admin"
    import Header from "../components/Header"

    export default {
        components: {
            Header
        },
        data(){
            const validateAddFormPass = (rule, value, cb) => {
                if(this.addFormData.password2 !== ""){
                    this.$refs["addForm"].validateField("password2")
                }
                cb()
            }

            const validateAddFormPass2 = (rule, value, cb) => {
                if(value !== this.addFormData.password){
                    cb(new Error("两次密码输入不一致"))
                }else{
                    cb()
                }
            }

            const validateEditPasswordFormPass  = (rule, value, cb) => {
                if(this.editPasswordFormData.password2 !== ""){
                    this.$refs["editPasswordForm"].validateField("password2")
                }
                cb()
            }

            const validateEditPasswordFormPass2 = (rule, value, cb) => {
                if(value !== this.editPasswordFormData.password){
                    cb(new Error("两次密码输入不一致"))
                }else{
                    cb()
                }
            }

            return {
                page: 1,
                size: 5,
                total: 0,
                admins: [],
                dialogAddFormVisible: false,
                dialogEditPasswordFormVisible: false,
                addFormData: {},
                editPasswordFormData: {},
                addLoading: false,
                editPasswordLoading: false,
                dataLoading: true,
                addFormRules: {
                    username: [
                        {required: true, message: "用户名不能为空",  trigger: 'change'}
                    ],
                    password: [
                        {required: true, message: "密码不能为空",  trigger: 'change'},
                        {validator: validateAddFormPass, trigger: 'blur'}
                    ],
                    password2: [
                        {required: true, message: "重复密码不能为空",  trigger: 'change'},
                        {validator: validateAddFormPass2, trigger: 'blur'}
                    ]
                },
                editPasswordFormRules: {
                    password: [
                        {required: true, message: "新密码不能为空",  trigger: 'change'},
                        {validator: validateEditPasswordFormPass, trigger: 'blur'}
                    ],
                    password2: [
                        {required: true, message: "重复新密码不能为空",  trigger: 'change'},
                        {validator: validateEditPasswordFormPass2, trigger: 'blur'}
                    ]
                }
            }
        },
        created(){
            this.getData()
        },
        methods: {
            async getData(){
                this.dataLoading = true
                const {page, size} = this
                try{
                    const data = await queryByPage(page, size)
                    this.admins = data.result
                    this.total = data.total
                }catch (e) {
                    return
                }finally {
                    this.dataLoading = false
                }
            },
            async remove(row){
                this.$set(row, "removeLoading", true)
                try{
                    await deleteAdmin(row.id)
                    this.$message.success("删除成功")
                }catch (e) {
                    return
                }finally {
                    this.$set(row, "removeLoading", false)
                }
                await this.getData()

            },
            async addAdmin(){
                this.addFormData = {
                    username: "",
                    password: "",
                    password2: ""
                }
                this.dialogAddFormVisible = true
                await this.$nextTick()
                this.$refs['addForm'].clearValidate()
                this.$refs['focus1'].focus()
            },
            async editPassword(row){
                this.editPasswordFormData = {
                    id: row.id,
                    username: row.username,
                    password: "",
                    password2: ""
                }
                this.dialogEditPasswordFormVisible = true
                await this.$nextTick()
                this.$refs['editPasswordForm'].clearValidate()
                this.$refs['focus2'].focus()
            },
            doAddAdmin(){
                this.$refs['addForm'].validate( async valid =>{
                    if(valid){
                        this.addLoading = true
                        try{
                            const {username, password} = this.addFormData
                            await addAdmin(username, password)
                            this.dialogAddFormVisible = false
                            this.$message.success("添加成功")
                        }catch (e) {
                            return
                        }finally {
                            this.addLoading = false
                        }
                        await this.getData()
                    }else{
                        return false;
                    }
                } )
            },
            doEditPassword(){
                this.$refs['editPasswordForm'].validate( async valid =>{
                    if(valid){
                        this.editPasswordLoading = true
                        try{
                            const {id, password} = this.editPasswordFormData
                            await changePassword(id, password)
                            this.dialogEditPasswordFormVisible = false
                            this.$message.success("修改成功")
                        }catch (e) {
                            return
                        }finally {
                            this.editPasswordLoading = false
                        }
                    }else{
                        return false;
                    }
                } )
            }
        }
    }
</script>

