import axiosInstance from "../config/axiosInstance"

export async function queryByPage(page, size) {
    const {data} = await axiosInstance.get(`/user/queryByPage?page=${page}&size=${size}`)
    return data.data;
}

export async function toggleStatus(id, status) {
    const {data} = await axiosInstance.post(`/user/toggleStatus`, {id, status})
    return data.data;
}

export async function sendVerificationCode(phone) {
    const {data} = await axiosInstance.post(`/user/sendVerificationCode`, {phone})
    return data.data;
}

export async function checkVerificationCode(phone, code) {
    const {data} = await axiosInstance.post(`/user/checkVerificationCode`, {phone, code})
    return data.data;
}