<template>
    <div>
        <Header title="类别管理"></Header>
        <el-button type="primary" @click="addCategory1">添加一级类别</el-button>
        <el-table
                :data="categories1"
                border
                stripe
                v-loading="data1Loading"
                ref="table1"
                @expand-change="tableExpand"
                style="width: 100%;margin-top: 20px">
            <template slot="empty">
                暂无数据，<el-link @click="getData1" type="primary">重新加载</el-link>
            </template>
            <el-table-column type="expand">
                <template slot-scope="scope">
                    <div class="cate2">
                        <h3>{{scope.row.cateName}} 分类下的二级类别：</h3>
                        <el-button type="primary" @click="addCategory2(scope.row)">添加二级类别</el-button>
                        <el-table
                                :data="categories2"
                                border
                                stripe
                                v-loading="data2Loading"
                                row-class-name="row-background"
                                style="margin-top: 20px">
                            <template slot="empty">
                                该分类下没有二级类别，<el-link @click="addCategory2(scope.row)" type="primary">点击添加</el-link>
                            </template>
                            <el-table-column
                                    label="二级类别id"
                                    min-width="120"
                                    align="center"
                                    prop="id">
                            </el-table-column>
                            <el-table-column
                                    label="二级类别名称"
                                    min-width="300"
                                    align="center"
                                    prop="cateName">
                            </el-table-column>
                            <el-table-column
                                    min-width="240"
                                    align="center"
                                    label="操作">
                                <template slot-scope="scope2">
                                    <el-button type="warning" @click="editName(scope2.row)">修改名称</el-button>
                                    &nbsp;
                                    <el-popconfirm
                                            :title="`确认删除类别${scope2.row.cateName}么？此操作不可恢复！`"
                                            @confirm="remove(scope2.row)"
                                    >
                                        <el-button
                                                :loading="scope2.row.removeLoading"
                                                type="danger"
                                                slot="reference">
                                            删除
                                        </el-button>
                                    </el-popconfirm>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="text-center" style="padding-top: 10px">
                            <el-pagination
                                    background
                                    :page-size.sync="size2"
                                    layout="total, prev, pager, next, jumper"
                                    :current-page.sync="page2"
                                    @current-change="getData2(scope.row.id)"
                                    hide-on-single-page
                                    :total="total2">
                            </el-pagination>
                        </div>
                    </div>
                </template>
            </el-table-column>
            <el-table-column
                    label="一级类别id"
                    min-width="120"
                    align="center"
                    prop="id">
            </el-table-column>
            <el-table-column
                    label="一级类别名称"
                    min-width="300"
                    align="center"
                    prop="cateName">
            </el-table-column>
            <el-table-column
                    min-width="240"
                    align="center"
                    label="操作">
                <template slot-scope="scope">
                    <el-button type="warning" @click="editName(scope.row)">修改名称</el-button>
                    &nbsp;
                    <el-popconfirm
                            :title="`确认删除类别${scope.row.cateName}么？此操作不可恢复！`"
                            @confirm="remove(scope.row)"
                    >
                        <el-button
                                :loading="scope.row.removeLoading"
                                type="danger"
                                slot="reference">
                            删除
                        </el-button>
                    </el-popconfirm>
                </template>
            </el-table-column>
        </el-table>

        <div class="text-center" style="padding-top: 10px;">
            <el-pagination
                    background
                    :page-size.sync="size1"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="page1"
                    @current-change="getData1"
                    hide-on-single-page
                    :total="total1">
            </el-pagination>
        </div>

        <el-dialog
                title="添加一级类别"
                width="400px"
                center
                :visible.sync="dialogAdd1FormVisible"
                :close-on-press-escape="false"
                :close-on-click-modal="false"
        >
            <el-form ref="add1Form" :model="addForm1Data" label-width="120px" label-position="left" :rules="add1FormRules" @keydown.native.enter="doAddCategory1">
                <el-form-item label="类别名称" prop="cateName">
                    <el-input v-model="addForm1Data.cateName" placeholder="请输入类别名称" ref="focus1"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button :loading="addLoading" type="primary" @click="doAddCategory1">添 加</el-button>
            </div>
        </el-dialog>

        <el-dialog
                title="添加二级类别"
                width="400px"
                center
                :visible.sync="dialogAdd2FormVisible"
                :close-on-press-escape="false"
                :close-on-click-modal="false"
        >
            <el-form ref="add2Form" :model="addForm2Data" label-width="120px" label-position="left" :rules="add2FormRules" @keydown.native.enter="doAddCategory2" v-loading="levels1Loading">
                <el-form-item label="所属一级类别" prop="parentId">
                    <el-select v-model="addForm2Data.parentId" placeholder="请选择所属一级类别">
                        <el-option
                                v-for="cate in levels1Data"
                                :key="cate.id"
                                :label="cate.cateName"
                                :value="cate.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="类别名称" prop="cateName">
                    <el-input v-model="addForm2Data.cateName" placeholder="请输入类别名称" ref="focus2"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button :loading="levels1Loading || addLoading" type="primary" @click="doAddCategory2">添 加</el-button>
            </div>
        </el-dialog>

        <el-dialog
                title="修改类别名称"
                width="400px"
                center
                :visible.sync="dialogEditNameVisible"
                :close-on-press-escape="false"
                :close-on-click-modal="false"
        >
            <el-form ref="editNameForm" :model="editNameFormData" label-width="100px" label-position="left" :rules="editNameFormRules"  @keydown.native.enter="doEditName">
                <el-form-item label="父类别ID" v-if="editNameFormData.parentId">
                    <el-input :value="editNameFormData.parentId" readonly></el-input>
                </el-form-item>
                <el-form-item label="ID">
                    <el-input :value="editNameFormData.id" readonly></el-input>
                </el-form-item>
                <el-form-item label="类别名称" prop="cateName">
                    <el-input v-model="editNameFormData.cateName" placeholder="请输入类别名称" ref="focus3"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button :loading="editNameLoading" type="primary" @click="doEditName">修 改</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {queryLevels, addCategory, deleteCategory, editName} from "../api/category"
import Header from "../components/Header"

let add2Pid = null

export default {
    components:{
        Header
    },
    data(){
        return {
            page1: 1,
            page2: 1,
            size1: 4,
            size2: 3,
            total1: 0,
            total2: 0,
            categories1: [],
            categories2: [],
            data1Loading: true,
            data2Loading: true,
            dialogAdd1FormVisible: false,
            addForm1Data: {},
            addForm2Data: {},
            editNameFormData:{},
            dialogAdd2FormVisible: false,
            dialogEditNameVisible: false,
            addLoading: false,
            editNameLoading: false,
            levels1Data: [],
            levels1Loading: true,
            add1FormRules: {
                cateName: [
                    {required: true, message: "类别名称不能为空",  trigger: 'change'}
                ]
            },
            add2FormRules: {
                cateName: [
                    {required: true, message: "类别名称不能为空",  trigger: 'change'}
                ],
                parentId: [
                    {required: true, message: "必须选择所属一级类别",  trigger: 'change'}
                ]
            },
            editNameFormRules: {
                cateName: [
                    {required: true, message: "类别名称不能为空",  trigger: 'change'}
                ]
            }
        }
    },
    created() {
        this.getData1()
    },
    methods: {
        async editName(row){
            this.editNameFormData = {
                id: row.id,
                cateName: row.cateName,
                levels: row.levels,
                parentId: row.parentId
            }
            this.dialogEditNameVisible = true
            await this.$nextTick()
            this.$refs['editNameForm'].clearValidate()
            this.$refs['focus3'].focus()
        },
        doEditName(){
            this.$refs['editNameForm'].validate( async valid =>{
                if(valid){
                    this.editNameLoading = true
                    try{
                        const {id, cateName, parentId} = this.editNameFormData
                        await editName(cateName, id, parentId)
                        this.dialogEditNameVisible = false
                        this.$message.success("修改成功")
                    }catch (e) {
                        return
                    }finally {
                        this.editNameLoading = false
                    }

                    const {levels, parentId} = this.editNameFormData
                    if(levels === 1){
                        await this.getData1()
                    }else{
                        await this.getData2(parentId)
                    }
                }else{
                    return false;
                }
            } )
        },
        async remove(row){
            this.$set(row, "removeLoading", true)
            try{
                await deleteCategory(row.id)
                this.$message.success("删除成功")
            }catch (e) {
                return
            }finally {
                this.$set(row, "removeLoading", false)
            }
            if(row.levels === 1){
                await this.getData1()
            }else{
                await this.getData2(row.parentId)
            }
        },
        tableExpand(row, expandedRows){

            this.page2 = 1
            this.getData2(row.id)

            for(let r of expandedRows){
                this.$refs["table1"].toggleRowExpansion(r, r.id === row.id)
            }
        },
        async getData1(){
            this.data1Loading = true
            const {page1, size1} = this
            try{
                const data = await queryLevels(page1, size1, 1)
                this.categories1 = data.result
                this.total1 = data.total
            }catch (e) {
                return
            }finally {
                this.data1Loading = false
            }
        },
        async getData2(id){
            this.data2Loading = true
            const {page2, size2} = this
            try{
                const data = await queryLevels(page2, size2, 2, id)
                this.categories2 = data.result
                this.total2 = data.total
            }catch (e) {
                return
            }finally {
                this.data2Loading = false
            }
        },
        async addCategory1(){
            this.dialogAdd1FormVisible = true

            this.addForm1Data = {
                cateName: "",
                levels: 1
            }

            await this.$nextTick()
            this.$refs['add1Form'].clearValidate()
            this.$refs['focus1'].focus()
        },
        async addCategory2(cate1){
            this.levels1Loading = true
            this.dialogAdd2FormVisible = true

            try {
                this.levels1Data = await queryLevels(null, null, 1, null)
            }catch (e) {
                return
            }finally {
                this.levels1Loading = false
            }

            this.addForm2Data = {
                cateName: "",
                levels: 2,
                parentId: cate1.id
            }

            add2Pid = cate1.id

            await this.$nextTick()
            this.$refs['add2Form'].clearValidate()
            this.$refs['focus2'].focus()
        },
        doAddCategory1(){
            this.$refs["add1Form"].validate( async valid =>{
                if(valid){
                    this.addLoading = true
                    const {cateName, levels} = this.addForm1Data
                    try{
                        await addCategory(cateName, levels, null)
                        this[`dialogAdd1FormVisible`] = false
                        this.$message.success("添加成功")
                    }catch (e) {
                        return
                    }finally {
                        this.addLoading = false
                    }
                    await this.getData1()
                }else{
                    return false;
                }
            } )
        },
        doAddCategory2(){
            this.$refs["add2Form"].validate( async valid =>{
                if(valid){
                    this.addLoading = true
                    const {cateName, levels, parentId} = this.addForm2Data
                    try{
                        await addCategory(cateName, levels, parentId)
                        this[`dialogAdd2FormVisible`] = false
                        this.$message.success("添加成功")
                    }catch (e) {
                        return
                    }finally {
                        this.addLoading = false
                    }
                    if(add2Pid === parentId){
                        await this.getData2(parentId)
                    }
                }else{
                    return false;
                }
            } )
        }
    }
}
</script>

<style scoped>
    .cate2{
        padding:20px;
        background: #f6fcf3;
        margin: -8px 0 -8px;
    }
</style>