function zeroNum(num) {
    if(num < 10){
        return "0" + num
    }

    return "" + num
}

export default function(date1) {
    const year = zeroNum(date1.getFullYear())
    const month = zeroNum(date1.getMonth() + 1)
    const date = zeroNum(date1.getDate())
    const hours = zeroNum(date1.getHours())
    const minutes = zeroNum(date1.getMinutes())
    const seconds = zeroNum(date1.getSeconds())

    return `${year}-${month}-${date} ${hours}:${minutes}:${seconds}`
}

