import axiosInstance from "../config/axiosInstance"

export async function queryLevels(page, size, levels, parentId) {
    let url = `/category/queryLevels?levels=${levels}`
    if(parentId != null){
        url += `&parentId=${parentId}`
    }
    if(page != null && size != null){
        url += `&page=${page}&size=${size}`
    }
    const {data} = await axiosInstance.get(url)
    return data.data;
}

export async function addCategory(cateName, levels, parentId) {
    const {data} = await axiosInstance.post(`/category/addCategory`, {cateName, levels, parentId})
    return data.data;
}

export async function editName(cateName, id, parentId) {
    const {data} = await axiosInstance.post(`/category/editName`, {cateName, id, parentId})
    return data.data;
}

export async function deleteCategory(id) {
    const {data} = await axiosInstance.get(`/category/deleteCategory?id=${id}`)
    return data.data;
}
