<template>
    <div>
        <Header title="日志管理"></Header>
        根据时间筛选：
        <el-date-picker
                v-model="timeFrame"
                type="datetimerange"
                :picker-options="pickerOptions"
                range-separator="至"
                @change="timeChange"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
        </el-date-picker>
        <el-table
                :data="logs"
                border
                stripe
                v-loading="dataLoading"
                style="width: 100%;margin-top: 20px">
            <template slot="empty">
                暂无数据，<el-link @click="getData" type="primary">重新加载</el-link>
            </template>
            <el-table-column
                    prop="id"
                    label="ID"
                    align="center"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    prop="adminName"
                    align="center"
                    label="操作者"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    align="center"
                    label="操作时间"
                    min-width="100">
                <template slot-scope="scope">
                    <div>{{scope.row.createTime.split(" ")[0]}}</div>
                    <div>{{scope.row.createTime.split(" ")[1]}}</div>
                </template>
            </el-table-column>
            <el-table-column
                    min-width="180"
                    align="center"
                    label="操作方法">
                <template slot-scope="scope">
                    <div>{{scope.row.methodName.split(" ")[0]}}</div>
                    <div>{{scope.row.methodName.split(" ")[1]}}</div>
                </template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="操作状态"
                    min-width="100">
                <template slot-scope="scope">
                    <span :class="{'fail': !scope.row.optionStatus, 'success': scope.row.optionStatus}">
                        {{scope.row.optionStatus ? "成功" : "失败"}}
                    </span>
                </template>
            </el-table-column>
        </el-table>

        <div class="text-center" style="padding-top: 10px;">
            <el-pagination
                    hide-on-single-page
                    background
                    :page-size.sync="size"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="page"
                    @current-change="getData"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import Header from "../components/Header"
    import {queryLog} from "../api/log"
    import timeFormat from "../util/timeFormat"

    let startTime, timeFrame

    export default {
        components:{
            Header
        },
        data(){
            startTime = new Date()
            startTime.setTime(startTime.getTime() - 3600 * 1000 * 24 * 30)

            timeFrame = [timeFormat(startTime), timeFormat(new Date())]

            return {
                logs: [],
                dataLoading: true,
                size: 10,
                page: 1,
                total: 0,
                timeFrame: [startTime ,new Date()],
                pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setHours(0, 0,0,0);

                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 91);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '今年',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(end.getFullYear(), 0);

                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '去年',
                        onClick(picker) {
                            const year = new Date().getFullYear() - 1
                            const end = new Date(year, 11,31, 23,59, 59, 999);
                            const start = new Date(year, 0);

                            picker.$emit('pick', [start, end]);
                        }
                    }]
                }
            }
        },
        created() {
            this.getData()
        },
        methods: {
            async getData() {
                this.dataLoading = true
                const {page, size} = this
                try {
                    const data = await queryLog(page, size, timeFrame[0], timeFrame[1])
                    this.logs = data.result
                    this.total = data.total
                } catch (e) {
                    return
                } finally {
                    this.dataLoading = false
                }
            },
            timeChange(frame){
                if(frame != null){
                    timeFrame[0] = timeFormat(frame[0])
                    timeFrame[1] = timeFormat(frame[1])
                    this.getData()
                }
            }
        }
    }
</script>

<style scoped>
    .fail{
        color: #f56c6c;
    }

    .success{
        color: #67c23a;
    }
</style>