<template>
    <div class="login-box">
        <div class="login-inner">
            <el-form ref="loginForm" :model="loginFormData" label-width="72px" label-position="left" :rules="loginFormRules" @keydown.native.enter="doLogin">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model.trim="loginFormData.username" placeholder="请输入用户名" ref="focus1"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="loginFormData.password" show-password placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item label="验证码" prop="code">
                    <el-input class="verify-code" v-model.trim="loginFormData.code" placeholder="请输入验证码"></el-input>
                    <img v-show="!verifyImageLoading" class="verify-image" :src="verifyImage" alt="点击换一张" title="点击换一张" @click="getVerifyImage()">
                </el-form-item>
            </el-form>
            <div class="text-center">
                <el-button :loading="loginLoading" type="primary" @click="doLogin">登 录</el-button>
            </div>
            <div class="welcome">
                欢迎使用应学后台管理系统
            </div>
        </div>
    </div>
</template>

<script>
    import {getVerifyImage, login} from "../api/admin"

    export default {
        data(){
            return {
                verifyImage: "",
                verifyImageLoading: true,
                loginLoading: false,
                loginFormData: {
                    username: "",
                    password: "",
                    code: ""
                },
                loginFormRules: {
                    username: [
                        {required: true, message: "用户名不能为空",  trigger: 'change'}
                    ],
                    password: [
                        {required: true, message: "密码不能为空",  trigger: 'change'}
                    ],
                    code: [
                        {required: true, message: "验证码不能为空", trigger: 'change'},
                        {pattern: /^[a-zA-Z0-9]{4}$/, message: "验证码为四个英文字母或数字", trigger: 'blur'}
                    ]
                }
            }
        },
        created(){
            window.document.title = `应学后台管理系统 - 登录`
            this.getVerifyImage()
        },
        mounted(){
            this.$refs['focus1'].focus()
        },
        methods: {
            async getVerifyImage(){
                this.verifyImageLoading = true
                try{
                    this.verifyImage = await getVerifyImage()
                }catch (e) {
                    return
                }finally {
                    this.verifyImageLoading = false
                }
            },
            doLogin(){
                this.$refs['loginForm'].validate( async valid =>{
                    if(valid){
                        this.loginLoading = true
                        try{
                            const {username, password, code} = this.loginFormData
                            this.$store.commit("setAdmin", await login(username, password, code))
                            this.$router.push("/")
                        }catch (e) {
                            this.getVerifyImage()
                            return
                        }finally {
                            this.loginLoading = false
                        }
                    }else{
                        return false;
                    }
                } )
            }
        }
    }
</script>

<style scoped>
    .login-box{
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        background-image: url("../assets/img/bg.jpg");
        background-position: center;
        background-size: cover;
    }

    .login-inner{
        width: 404px;
        border-radius: 10px;
        border: 2px solid #a0cfff;
        padding: 40px 40px 12px;
        background: rgba(217, 236, 255, 0.92);
    }

    .verify-image{
        width: 120px;
        height: 30px;
        float: right;
    }

    .verify-code{
        width: 120px;
    }

    .welcome{
        padding-top: 24px;
        text-align: center;
        color: #409eff;
    }
</style>