<template>
    <div id="main">
        <el-container v-loading="!currentAdmin.id">
            <el-container v-if="currentAdmin.id">
                <el-aside :width="menuCollapse? '64px' : '240px'">
                    <el-button v-show="!menuCollapse" @click="menuCollapse = true" size="mini" class="menu-collapse">&lt;</el-button>
                    <el-button v-show="menuCollapse" @click="menuCollapse = false" size="mini" class="menu-collapse">&gt;</el-button>
                    <el-menu
                            router
                            :collapse="menuCollapse"
                            :default-active="defaultActive"
                            :collapse-transition="false"
                            id="main-menu">
                        <el-menu-item index="/">
                            <i class="el-icon-s-home"></i>
                            <span slot="title">首页</span>
                        </el-menu-item>
                        <el-menu-item index="/user">
                            <i class="el-icon-user-solid"></i>
                            <span slot="title">用户管理</span>
                        </el-menu-item>
                        <el-menu-item index="/admin">
                            <i class="el-icon-s-custom"></i>
                            <span slot="title">管理员管理</span>
                        </el-menu-item>
                        <el-menu-item index="/category">
                            <i class="el-icon-menu"></i>
                            <span slot="title">类别管理</span>
                        </el-menu-item>
                        <el-menu-item index="/video">
                            <i class="el-icon-video-camera-solid"></i>
                            <span slot="title">视频管理</span>
                        </el-menu-item>
                        <el-menu-item index="/log">
                            <i class="el-icon-s-order"></i>
                            <span slot="title">日志管理</span>
                        </el-menu-item>
                    </el-menu>
                </el-aside>
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    import {me} from "../api/admin"

    export default {
        data(){
          return {
              defaultActive: "/",
              menuCollapse: false
          }
        },
        async created() {
            this.defaultActive = this.$route.name
            this.$store.commit("setAdmin", await me())
        }
    }
</script>

<style scoped>
    #main,.el-container,#main-menu,.el-aside,.el-main{
        height: 100%;
    }

    .el-aside{
        overflow: auto;
    }

    .el-main{
        overflow-y: scroll;
    }

    #main-menu{
        padding-top: 40px;
    }

    .el-aside{
        position: relative;
    }

    .menu-collapse{
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 1;
    }
</style>