import Vue from "vue"
import VueRouter from "vue-router"
import User from "../view/User"
import Admin from "../view/Admin"
import Main from "../view/Main"
import Home from "../view/Home"
import Login from "../view/Login"
import Category from "../view/Category"
import Video from "../view/Video"
import Log from "../view/Log"

Vue.use(VueRouter)

export default new VueRouter({
    routes: [
        {
            path: "/login",
            name: "/login",
            component: Login
        },
        {
            path: "/",
            component: Main,
            children: [
                {
                    path: "",
                    name: "/",
                    component: Home
                },
                {
                    path: "user",
                    name: "/user",
                    component: User
                },
                {
                    path: "admin",
                    name: "/admin",
                    component: Admin
                },
                {
                    path: "category",
                    name: "/category",
                    component: Category
                },
                {
                    path: "video",
                    name: "/video",
                    component: Video
                },
                {
                    path: "log",
                    name: "/log",
                    component: Log
                }
            ]
        }
    ]
})