<template>
    <div>
        <Header title="视频管理"></Header>
        <el-input placeholder="可以按视频标题搜索" clearable @change="getData" v-model="title" @clear="getData"></el-input>
        <el-table
                :data="videos"
                border
                stripe
                v-loading="dataLoading"
                style="width: 100%;margin-top: 20px">
            <template slot="empty">
                暂无数据，<el-link @click="getData" type="primary">重新加载</el-link>
            </template>
            <el-table-column
                    prop="id"
                    label="ID"
                    align="center"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    prop="title"
                    align="center"
                    label="视频标题"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    prop="description"
                    align="center"
                    label="视频描述"
                    min-width="160">
            </el-table-column>
            <el-table-column
                    min-width="180"
                    align="center"
                    label="视频预览">
                <template slot-scope="scope">
                    <video width="150px" height="150px" :src="scope.row.videoPath" :poster="scope.row.coverPath" controls/>
                </template>
            </el-table-column>
            <el-table-column
                    prop="category.cateName"
                    align="center"
                    label="分类"
                    min-width="100">
            </el-table-column>
            <el-table-column
                    prop="user.username"
                    align="center"
                    label="上传者"
                    min-width="100">
            </el-table-column>
            <el-table-column
                    align="center"
                    label="上传时间"
                    min-width="100">
                <template slot-scope="scope">
                    <div>{{scope.row.createTime.split(" ")[0]}}</div>
                    <div>{{scope.row.createTime.split(" ")[1]}}</div>
                </template>
            </el-table-column>
        </el-table>

        <div class="text-center" style="padding-top: 10px;">
            <el-pagination
                    hide-on-single-page
                    background
                    :page-size.sync="size"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="page"
                    @current-change="getData"
                    :total="total">
            </el-pagination>
        </div>

        <el-collapse v-model="activeNames">
            <el-collapse-item title="测试功能 上传视频" name="1">
                <div style="width: 360px">
                    <el-upload
                            ref="upload"
                            class="upload-demo"
                            drag
                            name="file"
                            :data="uploadData"
                            :auto-upload="false"
                            :limit="1"
                            :on-success="uploadSuccess"
                            :on-error="uploadError"
                            action="/api/video/upload">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em>点击选择</em></div>
                    </el-upload>
                    <div v-loading="cateDataLoading" style="display: inline-block">
                        视频标题：
                        <el-input class="video-title" v-model="uploadData.title" placeholder="请输入视频标题"></el-input>
                        一级类别：
                        <el-select v-model="cate1" placeholder="未选择" @change="cate1Change">
                            <el-option
                                    v-for="item in options1"
                                    :key="item.id"
                                    :label="item.cateName"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                        <br>
                        二级类别：
                        <el-select v-model="cate2" placeholder="未选择" @change="cate2Change">
                            <el-option
                                    v-for="item in options2"
                                    :key="item.id"
                                    :label="item.cateName"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                        <br>
                        <div class="text-center" style="padding-top: 20px;">
                            <el-button type="primary" @click="upload">上传</el-button>
                        </div>
                    </div>
                </div>
            </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script>
    import Header from "../components/Header"
    import {queryLevels} from "../api/category"
    import {searchVideo} from "../api/video"

    export default {
        data(){
            return {
                activeNames: ["1"],

                videos: [],
                dataLoading: true,
                size: 2,
                page: 1,
                total: 0,
                title: "",

                uploadData: {
                    userId: 1,
                    title: "测试标题",
                    categoryId: null,
                    description: "测试内容"
                },
                options1: [],
                options2: [],
                cateDataLoading: false,
                cate1: "",
                cate2: ""
            }
        },
        components:{
            Header
        },
        created(){
            this.getData()
            this.getCateData()
        },
        methods: {
            async getData(){
                this.dataLoading = true
                const {page, size, title} = this
                try{
                    const data = await searchVideo(page, size, title)
                    this.videos = data.result
                    this.total = data.total
                }catch (e) {
                    return
                }finally {
                    this.dataLoading = false
                }
            },
            async getCateData(){
                this.cateDataLoading = true

                try{
                    this.options1 = await queryLevels(null, null, 1)
                }catch (e) {
                    return
                }finally {
                    this.cateDataLoading = false
                }

            },
            async cate1Change(id1){
                this.uploadData.categoryId = id1
                this.cate2 = ""
                this.cateDataLoading = true
                try{
                    this.options2 = await queryLevels(null, null, 2, id1)
                }catch (e) {
                    return
                }finally {
                    this.cateDataLoading = false
                }
            },
            async cate2Change(id2){
                this.uploadData.categoryId = id2
            },
            upload(){
                if(this.uploadData.categoryId === null){
                    this.$message.error("请至少选择一级类别")
                    return
                }

                this.$refs["upload"].submit()
            },
            uploadSuccess(){
                this.$refs["upload"].clearFiles()
                this.getData()
            },
            uploadError(){
                this.$message.error("上传失败")
            }
        }
    }
</script>

<style scoped>
    .el-select, .video-title{
        width: 290px;
        margin-top: 10px;
    }
</style>