<template>
    <div>
        <Header title="用户管理"></Header>
        <el-table
                :data="users"
                border
                stripe
                v-loading="dataLoading"
                style="width: 100%;margin-top: 20px">
            <template slot="empty">
                暂无数据，<el-link @click="getData" type="primary">重新加载</el-link>
            </template>
            <el-table-column
                    prop="id"
                    label="ID"
                    align="center"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    prop="username"
                    align="center"
                    label="用户名"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    prop="sign"
                    align="center"
                    label="签名"
                    min-width="160">
            </el-table-column>
            <el-table-column
                    min-width="100"
                    align="center"
                    label="头像">
                <template slot-scope="scope">
                    <el-image :src="scope.row.headImg" style="width: 64px;height: 64px" :preview-src-list="[scope.row.headImg]"></el-image>
                </template>
            </el-table-column>
            <el-table-column
                    prop="phone"
                    align="center"
                    label="手机号"
                    min-width="120">
            </el-table-column>
            <el-table-column
                    align="center"
                    label="可用状态"
                    min-width="140">
                <template slot-scope="scope">
                    <el-switch
                            :value="scope.row.status"
                            @change="doToggleStatus(scope.row)"
                            active-text="可用"
                            inactive-text="冻结">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="注册时间"
                    min-width="100">
                <template slot-scope="scope">
                    <div>{{scope.row.createTime.split(" ")[0]}}</div>
                    <div>{{scope.row.createTime.split(" ")[1]}}</div>
                </template>
            </el-table-column>
        </el-table>
        <div class="text-center" style="padding-top: 10px;">
            <el-pagination
                    hide-on-single-page
                    background
                    :page-size.sync="size"
                    layout="total, prev, pager, next, jumper"
                    :current-page.sync="page"
                    @current-change="getData"
                    :total="total">
            </el-pagination>
        </div>

        <el-collapse v-model="activeNames">
            <el-collapse-item title="测试功能 验证手机号" name="1">
                <el-form style="width: 500px" ref="phoneForm" :model="phoneFormData" label-width="100px" label-position="left" :rules="phoneFormRules">
                    <el-form-item label="手机号" prop="phone">
                        <el-input v-model="phoneFormData.phone" class="small-input"></el-input>
                    </el-form-item>
                    <el-form-item label="验证码" prop="code">
                        <el-input style="width: 102px;margin-right: 6px" v-model="phoneFormData.code" class="small-input"></el-input>
                        <el-button style="width: 132px" :disabled="cd > 0" :loading="sendCodeLoading" @click="sendCode">{{cd > 0 ? `${cd}秒后可重新发送` : '发送验证码'}}</el-button>
                    </el-form-item>
                    <div class="text-center">
                        <el-button :loading="verifyLoading" type="warning" @click="doVerify">进行验证</el-button>
                    </div>
                </el-form>
            </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script>
    import Header from "../components/Header"
    import {queryByPage, toggleStatus, sendVerificationCode, checkVerificationCode} from "../api/user"
    let cdid = null

    export default {
        components:{
            Header
        },
        data(){
            return {
                activeNames: ["1"],

                users: [],
                dataLoading: true,
                size: 2,
                page: 1,
                total: 0,
                phoneFormData: {},
                phoneFormRules: {
                    phone: [
                        {required: true, message: "手机号不能为空", trigger: 'change'},
                        {pattern: /^1[0-9]{10}$/, message: "手机号格式不正确", trigger: 'blur'}
                    ],
                    code: [
                        {required: true, message: "验证码不能为空", trigger: 'change'},
                        {pattern: /^[0-9]{6}$/, message: "验证码为6位数字", trigger: 'blur'}
                    ]
                },
                sendCodeLoading: false,
                verifyLoading: false,
                cd: 0
            }
        },
        beforeDestroy() {
            if(cdid){
                clearInterval(cdid)
            }
        },
        created() {
            this.getData()
        },
        methods: {
            async getData(){
                this.dataLoading = true
                const {page, size} = this
                try{
                    const data = await queryByPage(page, size)
                    this.users = data.result
                    this.total = data.total
                }catch (e) {
                    return
                }finally {
                    this.dataLoading = false
                }
            },
            async doToggleStatus(row) {
                try {
                    await toggleStatus(row.id, !row.status)
                    if(row.status){
                        this.$message.success("冻结成功")
                    }else{
                        this.$message.success("解冻成功")
                    }
                } catch (e) {

                }
                this.getData()
            },
            sendCode(){
                this.$refs['phoneForm'].validateField("phone", async error =>{
                    if(!error){
                        this.sendCodeLoading = true
                        try{
                            const {phone} = this.phoneFormData
                            await sendVerificationCode(phone)
                            this.$message.success("发送成功")
                            this.cd = 60
                            cdid = setInterval(() => {
                                this.cd --
                                if(this.cd === 0){
                                    clearInterval(cdid)
                                }
                            }, 1000)
                        }catch (e) {
                            return
                        }finally {
                            this.sendCodeLoading = false
                        }
                    }else{
                        return false;
                    }
                } )
            },
            doVerify(){
                this.$refs['phoneForm'].validate(async valid =>{
                    if(valid){
                        this.verifyLoading = true
                        try{
                            const {phone, code} = this.phoneFormData
                            await checkVerificationCode(phone, code)
                            this.$message.success("验证成功")
                        }catch (e) {
                            return
                        }finally {
                            this.verifyLoading = false
                        }
                    }else{
                        return false;
                    }
                } )
            }
        }
    }
</script>

<style scoped>
    .small-input{
        width: 240px;
    }
</style>