import axiosInstance from "../config/axiosInstance"

export async function queryByPage(page, size) {
    const {data} = await axiosInstance.get(`/admin/queryByPage?page=${page}&size=${size}`)
    return data.data;
}

export async function deleteAdmin(id) {
    const {data} = await axiosInstance.get(`/admin/deleteAdmin?id=${id}`)
    return data.data;
}

export async function addAdmin(username, password) {
    const {data} = await axiosInstance.post(`/admin/addAdmin`, {username, password})
    return data.data;
}

export async function changePassword(id, password) {
    const {data} = await axiosInstance.post(`/admin/changePassword`, {id, password})
    return data.data;
}

export async function getVerifyImage() {
    const {data} = await axiosInstance.get(`/admin/getVerifyImage`)
    return data.data;
}

export async function login(username, password, code) {
    const {data} = await axiosInstance.post(`/admin/login?code=${code}`, {username, password})
    return data.data;
}

export async function logout() {
    const {data} = await axiosInstance.get(`/admin/logout`)
    return data.data;
}

export async function me() {
    const {data} = await axiosInstance.get(`/admin/me`)
    return data.data;
}