import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        admin: {}
    },
    mutations: {
        setAdmin(state, payload){
            state.admin = {
                id: payload.id,
                username: payload.username
            }
        },
        clearAdmin(state){
            state.admin = {}
        }
    }
})