<template>
    <div class="header">
        <h2>{{title}}</h2>
        <div class="current-admin">
            当前用户：{{currentAdmin.username}}
            &nbsp;
            <el-button :loading="logoutLoading" @click="doLogout">退出登录</el-button>
        </div>
    </div>
</template>

<script>
    import {logout} from "../api/admin"

    export default {
        data(){
            return {
                logoutLoading: false
            }
        },
        props: ["title"],
        created() {
            window.document.title = `应学后台管理系统 - ${this.title}`
        },
        methods: {
            async doLogout(){
                this.logoutLoading = true
                try{
                    await logout()
                    this.$store.commit("clearAdmin")
                    this.$router.push("/login")
                }catch (e) {
                    return
                }finally {
                    this.logoutLoading = false
                }
            }
        }
    }
</script>

<style scoped>
    .header{
        position: relative;
        height: 52px;
    }

    .current-admin{
        position: absolute;
        top: 0;
        right: 20px;
    }
</style>