import axiosInstance from "../config/axiosInstance"

export async function queryLog(page, size, startTime, endTime) {
    let url = `/log/queryByPage?page=${page}&size=${size}`
    if(startTime != null){
        url += `&startTime=${startTime}`
    }

    if(endTime != null){
        url += `&endTime=${endTime}`
    }

    const {data} = await axiosInstance.get(url)
    return data.data;
}